$primary-color : #de9d22;
$blue-color : #0d52a7;
$green-color : #208b73;
$red-color : #d1452e;
$gray-color : #dbdbdb;
$deep-gray-color:#444444;

@mixin footer_select {
    width: 130px;
    padding: 8px;
    color: #fff;
    border: 1px solid #fff;
    background: none;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

///////////////////////////////////////////////////////////////////////// 헤더
#header {
    position: relative;
    width: 100%;
    z-index: 3;
    &.border {
        border-bottom:1px solid #eaeaea;
    }
    #gnb {
        width: 1280px;
        margin: 0 auto;
        display: flex;
        padding: 15px 0;
        // 로고
        h1 {
            display: inline-block;
            margin-right: 30px;
        }
        ul {
            display: inline-block;
            vertical-align: top;
            margin-left: auto;
            li {
                display: inline-block;
                vertical-align: middle;
                font-weight: bold;
                font-size:15px;
            }
        }
        .gnb_menu {
            li {
                &:nth-child(2) {
                    margin: 0 35px;
                }
            }
        }
        .gnb_left {
            ul {
                li {
                    margin-right:40px;
                }
            }
        }
        .gnb_right {
            float: right;
            li {
                color:#666666;
                margin-right:8px;
                cursor: pointer;
                &:first-child {
                    margin-right:15px;
                }
                &:last-child {
                    margin-right:0px;
                }
                a {
                    padding:5px 15px; 
                    border-radius: 20px;
                    font-size:14px;
                    span {
                        vertical-align: top;
                    }
                }
            }
        }
    }
}

///////////////////////////////////////////////////////////////////////// 필터 옵션
.box_component_wrap {
    border-bottom: 1px solid #eaeaea;
    z-index: 3;
    background:#315f97;
    .box_component {
        width:1280px;
        margin:0 auto;
        padding:18px 0;
        ul {
            display:inline-block;
            li {
                display:inline-block;
                &:last-child {
                    margin-right:0;
                }
                .popup-wrap {
                    &.popup-closed {
                        button {
                            color:#b4b4b4;
                        }
                    }
                }
                button {
                    padding:10px 0;
                    font-size:14px;
                    color:#fff;
                    border:none;
                    img {
                        margin-right:5px;
                    }
                    &::after {
                        content:"";
                        display:inline-block;
                        width:2px;
                        height:20px;
                        margin:0 15px;
                        background:#466fa1;
                        vertical-align: middle;
                    }
                }
            }
        }
    }  
}

///////////////////////////////////////////////////////////////////////// main_version01
#main_wrap {
    background:url('../images/visual.png') no-repeat;
    background-size: 100%;
    height: 85vh;
}
#main_wrap02 {
    background:url('../images/visual02.png') no-repeat;
    #wrap {
        padding-top:11rem;
    }
}

///////////////////////////////////////////////////////////////////////// main_version02
#main {
    display:flex;
    min-height: 1000px;
    .main_left {
        min-width:965px;
        overflow:hidden;
        background:url('../images/left_background.png') no-repeat;
        background-size:cover;
        .main_left_contents {
            width:460px;
            height:100%;
            float:right;
            padding:9.5rem 0 0 0;
            margin-right:90px;
            a{
                margin: 40px 0;
            }
            p{
                color: #f5f5f5;
                font-size: 14px;
                margin-top:30px;
                line-height:25px;
            }
            h2{
                color: #fff;
                font-size: 62px;
                line-height: 4.5rem;
            }
            h3{
                color: #de9d22;
                font-size: 45px;
                font-weight:normal;
            }
            .autoplay3 {
                margin-top:9rem;
                .slick-list {
                    margin-top:50px;
                }
                .main_note {
                    display:flex;
                    padding-top:20px;
                    border-top:1px solid #fff;
                    .main_note_left {
                        h3 {
                            font-size:34px;
                            font-weight:bold;
                            color:#2f66aa;
                            line-height:25px;
                        }
                        span {
                            font-size:12px;
                            color:#fff;
                        }
                    }
                    .main_note_right {
                        p {
                            font-size:16px;
                            font-weight:300;
                            color:#fff;
                            margin:0 0 0 15px;
                        }
                    }
                }
                .slick-prev {
                    top:-20px;
                    left: 90%;
                    background:url('../images/slick_arrow_left.png') no-repeat;
                }
                .slick-next {
                    top: -20px;
                    right: 0;
                    background:url('../images/slick_arrow_right.png') no-repeat;
                }
            }
        }
    }
    .main_right {
        background:#f5f5f5;
        width:100%;
        padding:50px 0 50px 50px;
        overflow: hidden;
        h1 {
            font-size:24px;
            color:#333333;
            margin-bottom:20px;
        }
        .autoplay2 {
            margin-bottom:30px;
                .slick-prev {
                    top:-30px;
                    left: 60%;
                    background:url('../images/slick_arrow_left.png') no-repeat;
                }
                .slick-next {
                    top: -30px;
                    right: 35%;
                    background:url('../images/slick_arrow_right.png') no-repeat;
                }
            .main_my_class{
                .class_img{
                    display: flex;
                    position: relative;
                    overflow: hidden;
                    .class_time{
                        position: absolute;
                        top: 0;
                        margin-top: 9px;
                        color: #fff;
                        font-size: 11px;
                        border: none;
                        background: #2f66aa;
                        p{
                            padding: 4px 4px 4px 11px;
                            opacity: 0.8;
                            img{
                                margin-bottom: 2px;
                            }
                        }
                    }
                    .class_description{
                        position: absolute;
                        margin-left: 17px;
                        bottom: 25px;
                        p:first-child{
                            color: #fff;
                        }
                        p:last-child{
                            color: #fff;
                            font-size: 13px;
                            opacity: 0.5 ;
                        }
                    }
                }
            }
                .program_img{
                    display: flex;
                    .main_program_img {
                        background:#fff;
                        margin-right: 20px;
                        .program_img_top {
                            img {
                                width:100%;
                            }
                        }
                        .program_description {
                            position: relative;
                            padding:30px;
                            &.china {
                                &::before {
                                    content:'';
                                    position:absolute;
                                    top:-25px;
                                    left:20px;
                                    background:url(../images/main_country01.png) no-repeat;
                                    width:55px;
                                    height:55px;
                                }
                            }
                             &.korea {
                                &::before {
                                    content:'';
                                    position:absolute;
                                    top:-25px;
                                    left:20px;
                                    background:url(../images/main_country02.png) no-repeat;
                                    width:55px;
                                    height:55px;
                                }
                            }
                            h3{
                                color: #333333;
                                font-size: 17px;
                                margin-bottom: 10px;
                            }
                            p:last-child{
                                color: #2f66aa;
                                font-size: 13px;
                                font-weight: 700;
                            }
                        }
                    }
                    .main_program_img:last-child{
                        .program_img_top{
                            img{
                                height: 186.22px;
                            }
                        }
                    }
                }
            .main_info_top{
                h1{
                    display: inline-block;
                    margin-right: 140px;
                }
                img{
                    margin-left: 11px;
                    vertical-align: initial;
                }
            }
            .information_description{ 
                .info_card{
                    display: inline-block;
                    padding: 35px;
                    background: #bf5b4a;
                    border-radius: 8px;
                    span{
                        padding-right: 102px;
                        color: #fff;
                    }
                }
                .info_card_2{
                    display: inline-block;
                    padding: 35px;
                    background: #dca741;
                    border-radius: 8px;
                    span{
                        padding-right: 102px;
                        color: #fff;
                    }
                }
            }
        }
        div {
            h1{
                margin-bottom: 15px;
                font-size: 24px;
                margin-top: 40px;
            }
            &:first-child{
                h1 {
                    margin-top:0;
                }  
            }
        }
    }
}

///////////////////////////////////////////////////////////////////////// 로그인
.login {
    background:url(../images/login_background.png) no-repeat;
    background-size: 100%;
    background-position: center;
    .wrap {
        .login_wrap {
            width: 600px;
            margin:0 auto;
            text-align:center;
            padding:100px 0;
        }
    }
}

///////////////////////////////////////////////////////////////////////// footer
#footer {
    width: 100%;
    height: auto;
    background-color: #1b2023;
    #footer_wrap {
        position: relative;
        width: 1280px;
        margin: 0 auto;
        padding: 50px 0;
        .footer_info {
            display: inline-block;
            ul {
                display: inline-block;
                vertical-align: top;
                margin-right: 60px;
                font-size: 14px;
                li {
                    color: #fff;
                    margin: 10px 0;
                    &:first-child {
                        margin-top: 0;
                        margin-bottom: 30px;
                        font-weight: 700;
                    }
                    a {
                        color: #737373;
                    }
                }
                &:last-child {
                    li {
                        color: $primary-color;
                    }
                }
            }
        }
        .footer_right {
            float: right;
            color: #fff;
            .footer_select {
                text-align: right;
                margin-bottom: 20px;
                span {
                    margin-right:20px;
                }
                select {
                    @include footer_select;
                    option {
                        color:#000;
                    }
                }
            }
            .footer_sns {
                padding-top: 20px;
                color: #fff;
            }
        }
    }
    .footer_bottom {
        border-top: 1px solid #323739;
        color: #777777;
        font-size:14px;
        .footer_wrap {
            position: relative;
            width: 1280px;
            margin: 0 auto;
            padding: 30px 0;

            div {
                display: inline-block;
                &.footer_bottom_left {
                    img {
                        margin-right:15px;
                    }
                }
                &.footer_bottom_right {
                    float: right;
                }
            }
        }
    }

}


///////////////////////////////////////////////////////////////////////// 필터 옵션 레이어팝업
 .popup-wrap {
    position: relative;
    .popup-box-styles {
        position: absolute;
        left: 0;
        top: 3em;
        z-index: 999999;
        padding: 1em;
        text-align: left;
        background-color: #fff;
        color: #000;
        box-shadow: 0px 4px 8px 2px #6f6f6f33;
    }
    .filter {
         display:flex;
        overflow:hidden;
    }
    [aria-hidden=true] {
        display: none;
    }
}


///////////////////////////////////////////////////////////////////////// 마이페이지
.mypage {
    width: calc(100% - 290px);
    margin-left: 70px;
    > h3 {
        font-size:26px;
        font-weight:bold;
        margin-bottom: 30px;
        padding-bottom: 20px;
        border-bottom: 5px solid #eaeaea;
    }
}